.hero .graph-modal__container {
  background: rgba(179, 179, 179, 0.12);
  backdrop-filter: blur(20px);

  & .video-block {
    margin-bottom: 0;
    max-height: 500px;
    height: 100%;
  }

  & .graph-modal__close {
    background-image: url(../img/close.svg);
    background-repeat: no-repeat;
    width: 40px;
    height: 40px;
    z-index: 100;
  }
}

