.nav {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 500;
  overflow-y: auto;
  width: 100%;
  height: 717px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  background-color: rgba(21, 21, 21, 0.85);
  box-shadow: 0px 4px 40px rgb(0 0 0 / 25%);
  backdrop-filter: blur(20px);
  border-radius: 30px;

  &__content {
    display: flex;
    justify-content: space-between;
    padding-top: 130px;
    border-bottom: 3px solid rgba(255, 255, 255, .2);
    padding-bottom: 50px;
    flex-wrap: wrap;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    padding: 35px 50px;

    @include big-desktop {
      padding: 35px 50px 92px 50px;
    }
  }

  &__list {
    width: 33.33%;

    @include small-tablet {
      width: 100%;
    }

    &:last-child {

      @include big-desktop {
        width: 100%;

      }
    }
  }

  &__link {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 24px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &.menu--active {
    visibility: visible;
    opacity: 1;
  }

  &.menu--active+.header__burger {
    position: absolute;
    right: 100px;
  }
}

.nav.menu--active+.header__burger+.header__menu-text {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 500;
  font-size: 28px;
  line-height: 60px;
  margin-left: 10px;
  display: inline-block;
  background-color: rgba(21, 21, 21, 1);
  padding-top: 20px;
  padding-bottom: 22px;
  top: -12px;

  @include big-desktop {
    padding: 20px 92px 40px;
  }

  @include tablet {
    padding: 20px 20px 40px;
  }
}
