.form {
  background: rgba(179, 179, 179, 0.12);
  backdrop-filter: blur(10px);
  border-radius: 30px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px 33px;
  padding: 40px 30px 65px;
  position: relative;

  @include tablet {
    gap: 20px;
  }

  @include mobile {
    padding: 30px 20px 90px;
  }

  &__label {
    .choices {
      width: 100%;
    }

    .is-focused {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    .choices__inner {
      margin: 0;
      padding: 0;
      height: 60px;
      font-size: 14px;
      line-height: 150%;
      background: rgba(179, 179, 179, 0.12);
      border: 1px solid rgba(255, 255, 255, 0.6);
      backdrop-filter: blur(10px);
      border-radius: 20px;
      font-weight: 400;
      font-size: 20px;
      line-height: 34px;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.6);
    }

    .choices__list--single .choices__placeholder {
      font-size: 16px;
      line-height: 160%;
      color: #9a9ca5;
      opacity: 1;
    }

    .choices[data-type*=select-one] .choices__inner {
      padding-bottom: 0;
    }

    .choices__list--single {
      display: flex;
      align-items: center;
      padding-left: 15px;
      height: 100%;
    }

    .choices[data-type*=select-one]:after {
      right: 19px;
      margin-top: 0;
      border: none;
      width: 14px;
      height: 7px;
      background-image: url("../img/angle-down.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translateY(-50%);
      transition: transform 0.3s ease-in-out;
    }

    .choices[data-type*=select-one].is-open:after {
      margin-top: -7px;
      transform: rotate(180deg) translateY(-50%);
    }

    .choices__list--dropdown .choices__item--selectable:after {
      display: none;
    }

    .choices__list--dropdown {
      background: rgb(72 72 72);
      border: 1px solid rgba(255, 255, 255, 0.6);
      backdrop-filter: blur(10px);
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.6);
      border-radius: 20px;
      padding-top: 10px;
      padding-bottom: 10px;
    }

    .choices__list--dropdown .choices__item--selectable.is-highlighted,
    .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
      background: #FFFFFF;
      color: #040404;
    }

  }

  &__label--name {
    grid-column: 6 span;

    @include tablet {
      grid-column: 12 span;
    }
  }

  &__label--tel {
    grid-column: 6 span;

    @include tablet {
      grid-column: 12 span;
    }
  }

  &__label--date {
    grid-column: 4 span;

    @include tablet {
      grid-column: 9 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__label--time {
    grid-column: 2 span;

    @include tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__input {
    padding-left: 15px;
    width: 100%;
    height: 60px;
    background: rgba(179, 179, 179, 0.12);
    border: 1px solid rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 34px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.6);

    @include mobile {
      font-size: 18px;
      line-height: 32px;
    }

    &::placeholder {
      color: rgba(255, 255, 255, 0.6);
    }

    &::-webkit-calendar-picker-indicator {
      opacity: 0;
      display: none;
    }
  }

  &__btn {
    grid-column: 6 span;
    background: #FFFFFF;
    border-radius: 20px;
    font-size: 20px;
    line-height: 34px;
    text-transform: uppercase;
    color: #040404;
    padding: 13px;

    @include tablet {
      grid-column: 12 span;
    }

    @include mobile {
      margin-bottom: 5px;
    }
  }

  &__remark {
    position: absolute;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    color: #C4C4C4;
    bottom: 35px;
    right: 60px;

    @include mobile {
      right: 0;
      text-align: center;
      padding: 0 15px;
    }
  }
}
